export const remarkFrames = () => {
    return (tree) => {
        tree.children = tree.children.map((node) => {
            if (node.type === 'mdxJsxFlowElement' && (node.name === 'Example' || node.name === 'Frame')) {
                node.name = 'Frame';
            }
            return node;
        });
    };
};
